@import url('https://fonts.googleapis.com/css2?family=Anek+Tamil:wght@100..800&family=Noto+Sans+Tamil:wght@100..900&display=swap');

html,
body {
  min-height: 100%;
  margin: 0;
  font-family: 'Anek Tamil', 'Noto Sans Tamil', sans-serif;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
}

.App {
  text-align: center;
  flex-direction: column;
  min-height: 100vh;
}

.wrapper {
  flex: 1;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

h2 {
  font-size: 36px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

h2 {
  font-size: 28px !important;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}