.event-page,
.past-event-page {
  position: relative;
  padding-left: 10%;
  padding-right: 10%;
  max-width: 1400px;
  margin: 0 auto;
}

.event-page h2 {
  text-align: center;
}

.add-event-button {
  display: inline-block;
  margin-top: 20px;
  margin-bottom: 10px;
  padding: 10px 20px;
  background-color: #e88a1d;
  color: white;
  text-decoration: none;
  font-weight: 500;
  border-radius: 20px;
  cursor: pointer;
  border: none;
}

.event-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 40px;
  justify-items: center;
}

.event-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 20px;
  position: relative;
  width: 300px;
}

.image-container {
  width: 100%;
  padding-bottom: calc(100% * 5 / 4);
  position: relative;
  overflow: hidden;
  margin-bottom: 10px;
}

.image-container .event-image {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: bottom;
}

.member-only-text {
  position: absolute;
  top: 30px;
  left: 30px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  z-index: 1;
}

.event-details {
  text-align: left;
  color: #333;
  margin-bottom: 40px;
}

.event-details h3 {
  color: #e88a1d;
}

.event-actions {
  position: absolute;
  bottom: 20px;
  left: 15px;
  right: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.action-button {
  margin-right: 10px;
  padding: 5px 15px;
  background-color: #e88a1d;
  color: white;
  text-decoration: none;
  font-weight: 500;
  border-radius: 20px;
  cursor: pointer;
  border: none;
}

.event-card.no-actions {
  margin-bottom: 20px;
}

.event-card.no-actions .event-actions button {
  background-color: #ccc;
  pointer-events: none;
}

.menu-container {
  position: absolute;
  bottom: 17px;
  right: 15px;
}

.menu-icon {
  font-size: 20px;
  cursor: pointer;
}

.dropdown-menu {
  position: absolute;
  right: 0;
  bottom: 30px;
  background-color: white;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 10;
}

.menu-container:hover .dropdown-menu {
  display: block;
}

.dropdown-menu button {
  width: 100%;
  padding: 10px;
  text-align: left;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.dropdown-menu button:hover {
  background-color: #f2f2f2;
}

.add-edit-event-form {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
}

.add-edit-event-form h2 {
  text-align: center;
  margin-bottom: 20px;
}

.add-edit-event-form label {
  text-align: left;
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

.add-edit-event-form input,
.add-edit-event-form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.add-edit-event-form input[type="checkbox"] {
  width: auto;
}

.add-edit-event-form .form-actions {
  display: flex;
  justify-content: space-between;
}

.add-edit-event-form .form-actions .action-button {
  margin: 0;
}

.event_detail_modal,
.add_edit_event_modal {
  display: flex;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: flex-start;
}

.event-detail-modal-content {
  text-align: left;
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  max-width: 600px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.more-link {
  display: block;
  margin-top: 10px;
  text-decoration: none;
  color: #e88a1d;
  font-size: 16px;
  margin-bottom: 40px;
}

.more-link:hover {
  text-decoration: underline;
}

.filter-section {
  margin-bottom: 40px;
}

.date-filter {
  display: flex;
  gap: 10px;
  align-items: center;
}

.date-filter label {
  font-size: 16px;
}

.date-filter input[type="date"] {
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: none;
  outline: none;
}
